import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    user: Array,
    isLoggedIn: false,
    currentPage: String,
    transfer: {
      currency: "ETH",
      blockchainFee: "Regular",
      amount: 0,
    },
    exchange: {
      givenCurrency: "ETH",
      receivedCurrency: "BTC",
      givenAmount: 0,
      receivedAmount: 0
    },
    loadingButtonState: false,
  },
  getters: {
    getUser: state => {
      return state.user
    },
    getLoginState: state => {
      return state.isLoggedIn
    },
    getCurrentPage: state => {
      return state.currentPage
    },
    getTransferData: state => {
      return state.transfer
    },
    getExchangeData: state => {
      return state.exchange
    }
  },
  mutations: {
    setUser(state, user) {
      state.user = user
    },
    setLoginState(state, flag) {
      state.isLoggedIn = flag
    },
    setCurrentPage(state, page) {
      state.currentPage = page
    }, 
    setTransferData(state, data) {
      state.transfer = data
    },
    setExchangeData(state, data) {
      data.receivedAmount = 5 / data.givenAmount
      state.exchange = data
    }
  },
  actions: {}
});