<template>
  <b-form v-on:input="updateForm('setTransferData', transfer)" class="form-horizontal">
    <div class="row">
      <div class="col-sm-12">
        <div class="form-group">
          <label
            class="col-sm-12 col-md-6 control-label"
            :class="{'label-for-disabled': !isLoggedIn}"
          >Currency</label>
          <div class="col-sm-12 col-md-6">
            <select
              name="currency"
              v-model="transfer.currency"
              :disabled="!isLoggedIn"
              class="form-control"
            >
              <option
                v-for="(item, index) in currencies"
                :value="item.Value"
                :key="index"
              >{{ item.Label }}</option>
            </select>
          </div>
        </div>
        <div class="form-group">
          <label
            class="col-sm-12 col-md-6 control-label"
            :class="{'label-for-disabled': !isLoggedIn}"
          >Amount</label>
          <div class="col-sm-12 col-md-6">
            <input :disabled="!isLoggedIn" id="receiverName" class="form-control" />
          </div>
        </div>
        <div class="form-group">
          <label
            class="col-sm-12 col-md-6 control-label"
            :class="{'label-for-disabled': !isLoggedIn}"
          >Blockchain Fee</label>
          <div class="col-sm-12 col-md-6">
            <select
              name="blockchainFee"
              v-model="transfer.blockchainFee"
              :disabled="!isLoggedIn"
              class="form-control"
            >
              <option
                v-for="(item, index) in fees"
                :value="item"
                :key="index"
              >{{ item }}</option>
            </select>
          </div>
        </div>
        <div class="form-group">
          <div class="offset-md-6 col-sm-6 col-md-6">
            <input type="checkbox" id="termsAgreement" />
            <label for="termsAgreement">Terms of Service</label>
          </div>
        </div>
        <div class="offset-md-6 col-md-6 col-sm-6">
          <div class="form-group">
            <b-btn class="btn-block btn-lg btn-primary" :disabled="!isLoggedIn" type="submit">
              TRANSFER
              <span class="loader" :class="{loading: isLoading}"></span>
            </b-btn>
          </div>
        </div>
      </div>
    </div>
  </b-form>
</template>
<script>
import { mapGetters } from "vuex";
export default {
  data() {
    return {
      isLoading: false,
      fees: ['Regular', 'Economy', 'Priority'],
      currencies: [
        {
          Label: "Ethereum (ETH)",
          Value: "ETH"
        },
        {
          Label: "Bitcoin (BTC)",
          Value: "BTC"
        }
      ]
    };
  },
  computed: mapGetters({
    isLoggedIn: "getLoginState",
    transfer: "getTransferData",
  })
};
</script>

<style scoped>
.form-control:focus {
  box-shadow: 0 0 0 0rem rgba(143, 136, 206, 0.17);
  border: transparent;
}
@media (max-width: 768px) {
  #termsAgreement {
    margin-top: 15px;
  }
}
</style>

